@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500);
:root {
  --darker-light: #E3E6E9;
  overflow-y: scroll;
}

body {
  font-family: 'Roboto', sans-serif;
}

a {
  display: block;
  color: inherit;
}

a:hover {
  color: inherit;
  text-decoration: none;
}

h2 {
  text-align: center;
  font-weight: 500;
}

.box {
  background: var(--light);
  border: 2px solid var(--gray);
  border-radius: 5px;

  -webkit-transition: background 1s;

  transition: background 1s;
}

.box:hover {
  background: #E3E6E9;
  background: var(--darker-light);
}

.box--not-hover {
  background: var(--light) !important;
}

.Navbar {
  margin: 20px 0;
  font-size: 1.5rem;
}

.Navbar__link {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.Navbar__logo,
.Navbar__title {
  font-size: inherit;
}

.Navbar__title {
  margin: 10px 15px;
  font-weight: normal;
}

@media (min-width: 576px) {
  .Navbar {
    font-size: 2rem;
  }

  .Navbar__link {
    -webkit-flex-direction: row;
            flex-direction: row;
  }

  .Navbar__title {
    margin-top: 0 15px;
  }
}

@media (min-width: 768px) {
  .Navbar__link {
    margin: 0;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
  }
}

.LanguagesItem {
  height: 200px;
  margin-bottom: 20px;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;

  color: black;
}

.LanguagesItem__image {
  height: 75px;
}

.LanguagesItem__name {
  margin-top: 10px;

  text-transform: uppercase;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 300;
  letter-spacing: 3px;
}

@media (min-width: 992px) {
  .LanguagesItem__image {
    height: 85px;
  }
}

@media(min-width: 1200px) {
  .LanguagesItem__image {
    height: 100px;
  }
}

.Languages__Row {
  width: 85%;
  margin: 0 auto;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.GitHubUser {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.GitHubUser__img {
  height: 20px;
  margin-left: 5px;
}

.RepositoriesStars {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;

  font-size: 1.25rem;
}

.RepositoriesStars__icon {
  margin-left: 5px;
}

.RepositoriesItem__container {
  -webkit-align-items: center;
          align-items: center;

  padding: 10px 10px;
  margin-bottom: 10px;
}

.RepositoriesItem__name {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.RepositoriesItem__name,
.RepositoriesItem__stars {
  font-size: 1rem;
}

.RepositoriesItem__description {
  margin-top: 15px;

  overflow: hidden;
  text-overflow: ellipsis;
}

.RepositoriesItem__stars {
  margin-top: 15px;
}

@media (min-width: 576px) {
  .RepositoriesItem__container {
    padding: 20px 40px;
  }

  .RepositoriesItem__stars {
    margin-top: 0;
  }

  .RepositoriesItem__name,
  .RepositoriesItem__stars {
    font-size: 1.25rem;
  }
}

@media (min-width: 992px) {
  .RepositoriesItem__name,
  .RepositoriesItem__stars {
    font-size: 1.5rem;
  }
}

@media (min-width: 1200px) {
  .RepositoriesItem__description {
    margin-top: 0;
  }
}

.LoadingCircle {
  width: 0;
  margin: 100px auto 0;
  text-align: center;
}

.LoadingCircle__icon {
  font-size: 5rem;
  -webkit-animation: circle-rotation 1s linear infinite;
          animation: circle-rotation 1s linear infinite;
}

@-webkit-keyframes circle-rotation {
from {
  -webkit-transform: rotateZ(0deg);
          transform: rotateZ(0deg);
}

to {
  -webkit-transform: rotateZ(360deg);
          transform: rotateZ(360deg);
}
}

@keyframes circle-rotation {
from {
  -webkit-transform: rotateZ(0deg);
          transform: rotateZ(0deg);
}

to {
  -webkit-transform: rotateZ(360deg);
          transform: rotateZ(360deg);
}
}


.Repositories {
  width: 85%;
  margin-left: auto;
  margin-right: auto;
}

.AuthorContainer {
  width: 100%;
  height: 100%;
  padding: 20px 0;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.AuthorContainer__name,
.AuthorContainer__link-icon {
  height: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.AuthorContainer__name {
  font-size: 1.25rem;
}

.AuthorContainer__link-icon {
  font-size: 2rem;
}

@media (min-width: 768px) {
  .AuthorContainer__name {
    font-size: 2rem;
  }
  
  .AuthorContainer__link-icon {
    font-size: 3rem;
  }  
}

.AuthorContainer__avatar {
  width: 100%;
}

.Repository {
  width: 85%;
  margin: 0 auto;
}

.Repository__name-lang__link {
  margin: 0 -15px;
}

.Repository__description {
  margin: 20px -15px;
  padding: 20px 10px;
  font-size: 1.3rem;
}

.Repository__name-lang {
  font-size: 1.25rem;
}

@media (min-width: 768px) {
  .Repository__name-lang {
    font-size: 1.5rem;
  }  
}

@media (min-width: 992px) {  
  .Repository__stars,
  .Repository__watchers,
  .Repository__issues,
  .Repository__forks {
    font-size: 1.5rem;
  }  
}

.APILimitedMsg {
  text-align: center;
  color: darkred;
  font-size: 1.5rem;
}

.APILimitedMsg__icon {
  font-size: 3rem;
}

@media (min-width: 768px) {
  .MainContainer {
    margin-top: 100px;
  }
}


